.container_login{
        width: 100%;
        position: relative;
        top:20rem;
        height: calc(100vh - 100rem);
        background-color:inherit;
        display: flex;
        justify-content: center;
        align-items: center;
}
.login_form_container {
    width: 900px;
    height: 500px;
    display: flex;
    border-radius: 10px;
    box-shadow: 0px 3px 3px -2px rgb(0 0 0 /20%),
        0px 3px 4px 0px rgb(0 0 0 /14%),
        0px 1px 8px 0px rgb(0 0 0 /12%);
}

.left {
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.form_container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.form_container h1 {
    font-size: 40px;
    margin-top: 0;
}
.input {
    outline: none;
    border: none;
    width: 370px;
    padding: 15px;
    border-radius: 10px;
    background-color: #edf5f3;
    margin: 5px 0;
    font-size: 14px;
}
.error_msg {
    width: 370px;
    padding: 15px;
    margin: 5px 0;
    font-size: 14px;
    background-color: #f34646;
    color: white;
    border-radius: 5px;
}
.success_msg {
    width: 370px;
    padding: 15px;
    margin: 5px 0;
    font-size: 14px;
    background-color:hsl(120, 95%, 65%);
    color: white;
    border-radius: 5px;
}
.checkbox {
    width: 370px;
    padding: 10px;
    margin: 2px 0;
}
.right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color:#3bb19b;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}
.right h1 {
    margin-top: 0;
    color: white;
    font-size: 35px;
    align-self: center;
}
.green_btn,
.white_button {
    border: none;
    outline: none;
    padding: 12px 0;
    background-color: white;
    border-radius: 20px;
    width: 180px;
    font-weight: bold;
    cursor: pointer;
    font-size: 14px;
}

.green_btn {
    background-color: #3bb19b;
    color: white;
    margin: 10px;
}

/* ==========================landscape media query============================= */

@media screen and (orientation: portrait) {
  /* =======================================medias query for this page ifame===================================== */
  /* ===================================SMALL MEDIA QUERY=============================== */
  @media screen and (max-width: 20rem) {
   *{
    border: 1px solid red;
   }
  }
  /* =================================MEDIUM MEDIA QUERY================================================= */
  @media screen and (max-width: 31.25em) {
.container_login{
        width: 100%;
        position: relative;
        top:20rem;
        height: calc(100vh - 100rem);
        background-color:inherit;
        display: flex;
        justify-content: center;
        align-items: center;
}
.login_form_container {
    width: 900px;
    height: 500px;
    top: 0;
    display: block;
    border-radius: 0px;
    box-shadow: 0px;
}
.left {
padding: 1rem;
}
.form_container{
    padding: 0 5px;
}
.form_container h1 {
    font-size: 25px;
    margin-top: 0;
    padding:10px;
}
.input {
    margin: 5px 10px;
    font-size: 1.2rem;
}

.right ol{
    padding:10px;
    width: 80%;
    height: 100%;
}
  }

  /* ====================================TABLETE MEDIA QUERY====================================*/
  @media screen and (max-width: 50em) and (min-width: 32em) {
   *{
    border: 1px solid yellowgreen;
   }
 }
}


